// Theme Builder scripted functionality for DEMO purposes only.

(($) => {

    /*
    Check for clipboard permission and hide copy buttons if not found.
     */
    if (!navigator.clipboard) {
        $('.copy-to-clipboard').hide();
    }

    /*
    When a theme selector button is clicked,
    post the theme to settings and reload the page.
     */
    const buttons = $('.theme-selector-btn');
    buttons.on('click', event => {
        event.preventDefault();
        const theme = event.target.value;
        const form = $(event.target).closest('form');
        const url = form.attr('action');
        if (theme && url) {
            window.fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({theme}),
            })
                .then(r => {
                    window.location.reload();
                })
                .catch(e => {
                    console.error('unable to change theme:', e.message);
                })
            ;
        }
    });

})(jQuery);
